

.selector-title {
  padding: 8px 10px 8px 15px;
  background-color: #fcfcfc;
  border: 1px solid #d0d0d0;
  border-radius: 2px;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  cursor: pointer;
}

.selector-title:hover {
  color: #007bff;
}

.selector-title:active {
  color: #0058cc;
}

.selector-item.selected > .selector-title {
  background-color: #007bff;
  color: #fff;
}

.selector-item > .selector-item > .selector-title {
  margin: 10px 0px 10px 24px;
}
