
.SwatchEditor {
  border: 1px solid #bbb;
  border-radius: 3px;
  background: #fff;
  padding: 8px;
}

.swatch-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.swatch-title {
  border: none;
  line-height: 1;
  font-size: 13px;
  font-weight: 600;
  display: block;
  width: 100%;
  padding: 4px 0px;
}

.swatch-header .remove {
  height: 18px;
  width: 18px;
  text-align: center;
  font-size: 12px;
  line-height: 18px;
  border-radius: 2px;
  background: #cfcfcf;
  cursor: pointer;
}

.swatch-header .remove:hover {
  background-color: #ff4444;
  color: #fff;
}

.style-list {
  margin-top: 6px;
}

.styles-subtitle {
  font-size: 12px;
  color: #888;
  border-bottom: 1px dotted #cfcfcf;
  display: block;
  padding-bottom: 3px;
}

.add-style {
  display: block;
  width: 50%;
  padding: 4px 0px;
  border-radius: 3px;
  margin: 8px auto 0;
  background-color: #007bff;
  border-color: #007bff;
  color: #fff;
}

.add-style:hover, .add-style:focus {
  background-color: #0062cc;
  border-color: #0062cc;
}
