
.Tutorial {
  z-index: 1;
  position: absolute;
  background-color: #333;
  color: #efefef;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 0px 0px 4px 4px;
  display: flex;
  min-width: 50%;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 1px 5px -1px #007bff;
  padding: 10px 8px;
}

.tutorial-message {
  font-size: 12px;
  line-height: 1.4;
  margin: 0px 16px;
}

.tutorial-cta {
  white-space: nowrap;
}

.tutorial-btn {
  background: transparent;
  color: #efefef;
  border: none;
  padding: 0px 8px;
  cursor: pointer;
}

.tutorial-btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.tutorial-btn:hover {
  color: #007bff;
}
