
.ContextBuilder {
  height: 100%;
}

.context-cta {
  width: 100%;
  margin-bottom: 30px;
  font-size: 14px;
  font-style: italic;
  color: #6f6f6f;
}

.context-menu {
  margin-bottom: 20px;
}

.ctx {
  display: flex;
  justify-content: space-between;
  padding: 8px 10px 8px 15px;
  user-select: none;
  cursor: grab;
  font-size: 14px;
  border: 1px solid #d0d0d0;
  background: #fcfcfc;
  border-radius: 2px;
}

.ctx-input {
  font-size: 14px;
  background: transparent;
  border: none;
  line-height: 1;
  margin-right: 16px;
  cursor: grab;
}

.ctx-input:focus {
  cursor: text;
  color: #007bff;
}

.ctx-remove {
  line-height: 17px;
  padding: 0px 5px;
  cursor: pointer;
  color: #888;
  opacity: 0;
  transition: opacity 200ms linear;
}

.ctx:hover .ctx-remove {
  opacity: 1;
}

.add-context {
  padding: 5px 12px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
}

.add-context:hover, .add-context:focus {
  background-color: #0062cc;
  border-color: #0062cc;
}

.ContextBuilder .divider {
  height: 10px;
  position: relative;
}

.ContextBuilder .divider:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0px;
  left: 0px;
  border-bottom: 1px solid #007bff;
  opacity: 0;
}

.ContextBuilder .divider.dragged:before {
  opacity: 1;
}
