
.StyleEditor {
  display: flex;
  z-index: 0;
  position: relative;
}

.StyleEditor:focus-within {
  z-index: 100;
}

.style-input {
  flex: 1;
  position: relative;
}

.style-input input {
  width: 100%;
  display: block;
  border: none;
  padding: 4px;
  text-overflow: ellipsis;
}

.style-input input:focus {
  color: #007bff;
}

.dom-key input {
  padding-left: 0px;
}

.dom-value input {
  padding-right: 0px;
  text-align: right;
}

.dom-key input.focused + .key-dropdown {
  display: block;
}

.key-dropdown {
  position: absolute;
  display: none;
  padding: 5px 0px;
  background: #fff;
  border: 1px solid #aaa;
  border-radius: 5px;
  box-shadow: 0px 1px 3px -1px #aaa;
  max-height: 150px;
  overflow-x: hidden;
}

.key-item {
  font-size: 11px;
  padding: 4px 8px;
  cursor: pointer;
  color: #555;
}

.key-item:hover {
  background-color: #007bff;
  color: #fff;
}
