
.ElementBuilder {
  height: 100%;
}

.elements-list {
  display: flex;
}

.elements-list button {
  padding: 0px 12px;
  height: 36px;
}

.element-item-label {
  padding: 8px 10px 8px 15px;
  display: flex;
  justify-content: space-between;
  cursor: grab;
  user-select: none;
  font-size: 14px;
  border: 1px solid #d0d0d0;
  background: #fcfcfc;
  border-radius: 2px;
}

.element-item-input {
  font-size: 14px;
  background: transparent;
  border: none;
  line-height: 1;
  margin-right: 16px;
  cursor: grab;
}

.element-item-input:focus {
  cursor: text;
  color: #007bff;
}

.element-item-label:hover .element-item-icon {
  opacity: 1;
}

.element-item-icon {
  line-height: 17px;
  padding: 0px 5px;
  cursor: pointer;
  color: #888;
  opacity: 0;
  transition: opacity 200ms linear;
}

.element-item-icon:hover {
  color: #007bff;
}

.dragged > .element-item-label .element-item-input {
  color: #007bff;
}

.element-item-divider {
  padding: 5px 0px;
  position: relative;
  display: block;
}

.element-item-divider:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0px;
  right: 0px;
  border-bottom: 1px solid transparent;
}

.element-item-divider.dragged:after {
  border-bottom-color: #007bff;
}

.element-item > .element-item {
  padding-left: 24px;
}

.add-element {
  padding: 4px 0px;
  border-radius: 3px;
  background-color: #007bff;
  border-color: #007bff;
  color: #fff;
}

.add-element:hover, .add-element:focus {
  background-color: #0062cc;
  border-color: #0062cc;
}

.element-cta {
  width: 100%;
  margin-bottom: 30px;
  font-size: 14px;
  font-style: italic;
  color: #6f6f6f;
}

.element-cta-button {
  position: relative;
  background: none;
  border: none;
  font-size: 14px;
  color: #007bff;
  margin-left: 5px;
}

.element-cta-button.open .template-options {
  display: block;
}

.template-options {
  display: none;
  position: absolute;
  top: calc(100% + 6px);
  background: #fff;
  text-align: left;
  border-radius: 3px;
  padding: 6px 0px;
  box-shadow: 0px 1px 3px -1px #aaa;
}

.template-options li {
  color: #333;
  padding: 4px 15px;
}

.template-options li:hover {
  background-color: #007bff;
  color: #fff;
}
