
.Setup {
  height: 100%;
  padding: 50px 30px 30px;
}

.Setup .intro {
  width: 100%;
  height: 32px;
  text-align: center;
  font-size: 14px;
}

.Setup .setup-body {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.Setup .setup-section {
  flex: 1;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 1px 3px -1px #888;
  padding: 20px;
  min-width: 360px;
  flex: 0;
  margin: 0px 20px;
  text-align: center;
}

.Setup .section-title {
  font-size: 20px;
  font-weight: 600;
  color: #333;
  margin-bottom: 30px;
}

.section-title small {
  display: block;
  font-size: 12px;
  font-weight: 300;
  margin-top: 4px;
}

.Setup .setup-button {
  padding: 6px 12px;
  background: #007bff;
  color: #fff;
  font-size: 12px;
  border: none;
  border-radius: 3px;
  position: relative;
}

.Setup .setup-button.disabled {
  background-color: #888;
  opacity: 0.5;
  pointer-events: none;
}

.Setup .load {
  margin-left: 8px;
}

.Setup .load i {
  margin-left: 6px;
}

.Setup .create {
  margin-top: 12px;
}

.Setup .load.open .load-options {
  display: block;
}

.Setup .load-options {
  display: none;
  position: absolute;
  top: calc(100% + 6px);
  border-radius: 4px;
  padding: 6px 0px;
  background-color: #fff;
  box-shadow: 0px 1px 3px -1px #aaa;
}

.Setup .load-option {
  padding: 3px 8px;
  color: #333;
  white-space: nowrap;
  text-align: left;
}

.Setup .load-option:hover {
  background-color: #007bff;
  color: #fff;
}

.name-label {
  display: block;
}

.name-input {
  width: 100%;
  padding: 5px;
  font-size: 12px;
  line-height: 1;
  margin-right: 8px;
  border-radius: 2px;
  border: 1px solid #ddd;
}
