
.NavTab {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.tab-body {
  padding: 40px;
  flex: 1;
}

.tab-header {
  position: relative;
  height: 106px;
  padding-top: 14px;
  padding-left: 40px;
  border-bottom: 1px solid #cdcdcd;
  background: #fff;
}

.meta-info input {
  padding: 0px 6px;
  font-size: 14px;
  line-height: 33px;
  height: 33px;
  vertical-align: top;
  margin-right: 10px;
  border-radius: 3px;
  border: 1px solid #ddd;
}

.tab-header-item {
  background: #f0f0f0;
  height: 44px;
  line-height: 58px;
  font-size: 14px;
  margin-right: 20px;
  padding: 0px 12px;
  border-radius: 3px 3px 0px 0px;
  border: 1px solid #cdcdcd;
  cursor: pointer;
  position: relative;
}

.tab-header-sub-title {
  border-radius: 3px 3px 0px 0px;
  position: absolute;
  top: 0px;
  font-size: 10px;
  left: 0px;
  right: 0px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  height: 14px;
  line-height: 14px;
  padding-left: 6px;
}

.Build.tab-header-sub-title {
  background-color: #ffd6a5;
  color: #a87219;
}

.Design.tab-header-sub-title {
  background-color: #e5d8ff;
  color: #5555aa;
}

.View.tab-header-sub-title {
  background-color: #b3ec28;
  color: #53660e;
}

.tab-header-item.active, .tab-header-item:hover {
  color: #007bff;
}

.tab-header-list {
  display: flex;
  margin-top: 15px;
}

.setup {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.build {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.style, .context {
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.state, .preview {
  height: 100%;
}

.style-section {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.style-section.expand {
  flex: 1;
}

.style-section:last-of-type {
  margin-right: 0px;
}

.style-section .subtitle {
  margin-bottom: 10px;
  font-size: 12px;
  color: #6f6f6f;
}
