
.ContextSelector .context-item {
  display: flex;
  justify-content: space-between;
  padding: 8px 10px 8px 15px;
  user-select: none;
  border: 1px solid #d0d0d0;
  background: #fcfcfc;
  border-radius: 2px;
  margin-bottom: 10px;
  cursor: pointer;
  color: #333;
}

.ContextSelector .context-item:hover {
  color: #007bff;
}

.ContextSelector .context-item.selected {
  background: #007bff;
  color: #fff;
}

.ContextSelector .context-item h3 {
  font-size: 14px;
  font-weight: normal;
}
