
.StateEditor {
  border: 1px solid #cfcfcf;
  border-radius: 3px;
  background-color: #fff;
}

.StateEditor.expanded .state-header {
  border-bottom: 1px dotted #ddd;
}

.StateEditor.expanded .state-body {
  display: block;
}

.StateEditor .state-header-icon {
  width: 16px;
  height: 16px;
  line-height: 16px;
  background-color: #cfcfcf;
  cursor: pointer;
  border-radius: 2px;
  font-size: 12px;
  text-align: center;
}

.state-header-icon.indicator {
  margin-right: 8px;
  font-size: 8px;
  color: #cfcfcf;
}

.state-header-icon.expand {
  margin-left: 16px;
}

.state-header-icon.remove {
  margin-left: 8px;
}

.state-header-icon.remove:hover {
  background-color: #ff4444;
}

.state-header-icon:hover {
  background-color: #007bff;
  color: #fff;
}

.StateEditor.selected .state-header-icon.indicator {
  background-color: #007bff;
  color: #fff;
}

.state-header {
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
  color: #333;
  padding: 5px 12px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.state-header-input {
  font-size: 14px;
  line-height: 1;
  padding: 0px;
  border: none;
  font-weight: 600;
}

.state-header-input:focus {
  color: #007bff;
}

.state-body {
  display: none;
}

.StateEditor.expanded .state-body {
  padding: 8px 0px;
}

.state-context-item {
  padding: 0px 12px;
  height: 28px;
  line-height: 1;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #6f6f6f;
  user-select: none;
}

.state-context-icon {
  cursor: pointer;
  margin-left: 6px;
  height: 16px;
  width: 16px;
  line-height: 16px;
  border-radius: 50%;
  background: #ff4444;
  text-align: center;
  color: #fff;
  font-size: 10px;
  opacity: 0;
  transition: opacity 200ms linear;
}

.state-context-icon:hover {
  background: #cc1111;
}

.state-context-item:hover .state-context-icon {
  opacity: 1;
}

.state-body .state-context-select {
  margin: 8px 12px 0px;
}
