
.SwatchBuilder {
  width: 320px;
  height: 100%;
  overflow-x: hidden;
}

.add-swatch {
  width: 80%;
  margin: 0 auto;
  display: block;
  padding: 8px 0px;
  border-radius: 3px;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  transition: all 300ms linear;
}

.add-swatch:hover {
  background-color: #0062cc;
  border-color: #0062cc;
}

.add-swatch.disabled {
  opacity: 0.5;
  background-color: #ddd;
  pointer-events: none;
}

.SwatchBuilder .SwatchEditor {
  margin-bottom: 20px;
}
