
.Preview {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.state-view-selector {
  display: flex;
  align-items: center;
  font-size: 14px;
  margin-bottom: 14px;
}

.state-view-menu {
  display: flex;
  margin: 0px 8px;
}

.state-view-item {
  margin: 0px 8px;
  cursor: pointer;
}

.state-view-item:hover {
  color: #4da3ff;
}

.state-view-item.active {
  color: #007bff;
  text-decoration: underline;
}

.Preview .Canvas {
  width: 100%;
}

.Preview .state-view-dir {
  padding: 4px 8px;
  cursor: pointer;
}

.Preview .state-view-dir:hover {
  color: #007bff;
}
